.item {
    width: calc(100% / 5);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.label {
    font-size: 13px;
    font-weight: bold;
    color: white;
}

.percentage {
    font-size: 11px;
    font-weight: bold;
    color: white;
}
